import { Injectable } from '@angular/core';
import { select } from '@ngrx/store';
import { Observable } from 'rxjs';

import { LoadMovementAction, ResetMovementAction } from '@store/actions/models/movements/movement.action';
import { ApplicationModel } from '@app/application.model';
import { Movement } from '@core/models/movement/movement';

@Injectable()
export class MovementsModel extends ApplicationModel {

    public account$: Observable<Movement> = this.store.pipe(
        select(store => store.models.movement.account)
    );

    public loading$: Observable<boolean> = this.store.pipe(
      select(store => store.models.movement.loading)
    );

    public fetchMovement(type: string, id: string): void {
        this.store.dispatch(new LoadMovementAction(type, id));
    }

    public clearMovement(): void {
      this.store.dispatch(new ResetMovementAction());
    }
}
