import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';


import { environment as ENV } from '@environment';
import { Movement } from '@core/models/movement/movement';

@Injectable()
export class MovementsService {

  constructor(private http: HttpClient) { }

  public movements(type: string, id: string): Observable<Movement> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json'
      })
    };

    const user = {
      'id': '19800203',
      'idType': 'CC',
      'customerId': '19800203',
      'customerIdType': 'CC',
      'accountId': id,
      'accountType': type.toUpperCase(),
      'currency': 'COP',
      'requestId': '341690',
      'companyId': 'BANCO_POPULAR',
      'ipAddress': '192.168.206.28'
    };

    return this.http.post<Movement>(ENV.api.base + ENV.api.services.movements, user, httpOptions);
  }
}
