import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { isNullOrUndefined } from 'util';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';

import { MovementsModel } from '@modules/movements/movements.model';
import { Movement } from '@core/models/movement/movement';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-movements-container',
  templateUrl: './movements.container.html',
  styleUrls: ['./movements.container.sass'],
  encapsulation: ViewEncapsulation.None
})
export class MovementsContainer implements OnInit, OnDestroy {

  public textFilter: string = '';

  constructor(
      private model: MovementsModel,
      private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.route.paramMap.subscribe(params => {
      if (!isNullOrUndefined(params.get('type') && params.get('id'))) {
        this.model.fetchMovement(params.get('type'), params.get('id'));
      }
    }).unsubscribe();
  }

  ngOnDestroy(): void {
    this.model.clearMovement();
  }

  get isloading$(): Observable<boolean> {
    return this.model.loading$;
  }

  get account$(): Observable<Movement> {
    return this.model.account$;
  }

  get hasAccount$(): Observable<boolean> {
    return this.account$.pipe(
      map((data: Movement) => !isNullOrUndefined(data) && !isNullOrUndefined(data.operations))
    );
  }

}
