import { catchError, map, switchMap } from 'rxjs/operators';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { Injectable } from '@angular/core';
import { Action } from '@ngrx/store';
import { Observable, of } from 'rxjs';

import * as movementActions from '@store/actions/models/movements/movement.action';

import { MovementsService } from '@modules/movements/services/movements/movements.service';
import { Movement } from '@core/models/movement/movement';

@Injectable()
export class MovementEffect {

    constructor(
        private actions$: Actions,
        private movementsService: MovementsService
    ) { }

    @Effect()
    LoadMovement: Observable<Action> = this.actions$.pipe(
        ofType<movementActions.LoadMovementAction>(movementActions.LOAD_MOVEMENT),
        switchMap((action: movementActions.LoadMovementAction) => {
            return this.movementsService.movements(action.typeAccount, action.accountId)
                .pipe(
                    map((accountMovement: Movement) => new movementActions.LoadMovementSuccessAction(accountMovement)),
                    catchError(_ => of(new movementActions.LoadMovementFailAction()))
                );
        })
    );
}
